import {BrowserRouter, Route, Switch,Redirect} from "react-router-dom";
import {App} from "../App";
import {AnimePage} from "../pages/AnimePage";
import {Navbar} from "./Navbar";
import {SearchPage} from "../pages/SearchPage";
import {FavoritePage} from "../pages/FavoritePage";
import {TestPage} from "../pages/TestPage";
import {Router} from "@mui/icons-material";


export const Root = ()=>{

    return(
        <BrowserRouter>
            <Navbar/>
            <Switch>
                <Route exact={true} path="/anime" component={App}/>
                    <Route exact path="/animeSite" >
                        <Redirect to="/anime"/>
                    </Route>
                <Route  path="/animeSite/:mal_id" component={AnimePage}/>
                <Route  path="/anime/:mal_id" component={AnimePage}/>
                <Route  path="/search" component={SearchPage}/>
                <Route  path="/favorite" component={FavoritePage}/>
                <Route path="/search/:id_genre" component={SearchPage}/>
                <Route path="/" component={TestPage} />
                <Route>
                   <h1>ERROR</h1>
                </Route>
            </Switch>
        </BrowserRouter>
    )
}