import axios from "axios";
import {createAsyncThunk} from "@reduxjs/toolkit";

const BASE_URL = 'https://api.jikan.moe/v4/'

export const fetchAnimelist = createAsyncThunk(
    'anime/fetchAll',
    async(page:number=1)=>{
        try{
            const {data} = await axios.get(`${BASE_URL}top/anime?page=${page}`)
            return data
        }catch(e){
            return 'error'
        }

    }
)


export const fetchAllGenres = createAsyncThunk(
    'anime/genres',
    async(_,)=>{
        try{
            const {data} =  await axios.get(`${BASE_URL}genres/anime`)
            return data
        }catch(e){
            return 'error'
        }
    }
)


export const fetchByParams = createAsyncThunk(
    'anime/fetchByParams',
    async({payload}:any)=>{
        console.log('payload',payload)
        const [currentGenres,currentYear,currentPage,currentOrder,currentSort] = payload

        const genres = currentGenres?(`genres=${currentGenres}`):('')
        const year = currentYear?(`&start_date=${currentYear}`):('')
        const page = currentPage?(`&page=${currentPage}`):(`&page=1`)
        const order = currentOrder?(`&order_by=${currentOrder}`):('')
        const sort = currentSort?(`&sort=${currentSort}`):('')

        try{
            const {data} =  await axios.get(`${BASE_URL}anime?${genres}${year}${page}${order}${sort}`)
            return data

        }catch(e){

        }
    }
)


export const fetchByName= createAsyncThunk(
    'anime/fetchByName',
    async({payload}:any)=>{
        try{
            const [text,page] = payload
            const  { data } = await axios.get(`${BASE_URL}anime?q=${text}&page=${page}`)
            return data
        }catch(e){
            return 'error'
        }

    }
)