import React, {useState} from 'react';
import { Box, InputBase} from "@mui/material";
import {Link, useRouteMatch} from "react-router-dom";
import {useHistory} from 'react-router-dom';
import {useAppDispatch} from "../hooks/redux";
import {fetchByName} from "../store/reducers/ActionCreators";
import {style} from './styleComponets'
export const Navbar = ()=>{

    const [text,setText]= useState('');

    const dispatch = useAppDispatch()
    const { path } = useRouteMatch();
    const history = useHistory()

    function handleInput(e){
        setText(e.target.value)
    }

    function handleKeyDown(e){
        if (e.key === "Enter") {

            const data = [text,1]
            dispatch(fetchByName({data}))
            const query = `${path}search`
            setText('')

            history.push(query,text)


        }

    }

    return(
        <Box sx={style.containerNavbar}>
            <Box >
                <InputBase sx={style.input} value={text} placeholder='search...' onChange={handleInput} onKeyDown={handleKeyDown}/>
            </Box>
            <Box sx={style.linkGroup}>
                <Link style={style.link}
                    to='/search'>
                    <Box>Genre</Box>
                </Link>
                <Link style={style.link}
                    to='/favorite'>
                    <Box>Favorite</Box>
                </Link>
                <Link
                    style={style.link}
                    to='/anime'>
                    <Box>Anime</Box>
                </Link>
            </Box>

        </Box>

    )
}