import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import {
    Box, CircularProgress,
    Container, Table,
    TableBody,
    TableCell,
    TableContainer,
    TableRow,
    Typography
} from "@mui/material";

import API from "../API";

import {style} from './stylePages'
import LinearProgress from "@mui/material/LinearProgress";

export const AnimePage = () => {

    const [isLoading, setIsLoading] = useState(true)
    const [data, setData] = useState()

    useEffect( () => {
        async function fetchData(){
            setIsLoading(true)
            const response = await API.fetchAnimeInfo(mal_id)
            setData(response)
            setIsLoading(false)
        }
        fetchData()
        console.log(data)
    }, [])

    const {mal_id} = useParams();

    const Card = () => {
        return (
            !isLoading ?(
                <>
                    <Container sx={style.main_container}>
                        <h2>{data.title}</h2>
                        <Box sx={style.container}>
                            <Box sx={style.img_container}>
                                <img alt='img' src={data.images.jpg.image_url}/>
                            </Box>
                            <Box sx={style.info_container}>

                                <TableContainer sx={style.table_container}>
                                    <Table>

                                        <TableBody>
                                            <TableRow>
                                                <TableCell component="th" scope="row">
                                                    <b>Rating:</b>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    {data.score}
                                                </TableCell>
                                            </TableRow>

                                            <TableRow>
                                                <TableCell component="th" scope="row">
                                                    <b>Airing:</b>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    {data.aired.string}
                                                </TableCell>
                                            </TableRow>

                                            <TableRow>
                                                <TableCell component="th" scope="row">
                                                    <b>Status:</b>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    {data.status}
                                                </TableCell>
                                            </TableRow>

                                            <TableRow>
                                                <TableCell component="th" scope="row">
                                                    <b>Producers:</b>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    {data.producers.map(item => {
                                                        return `${item.name}, `
                                                    })}
                                                </TableCell>
                                            </TableRow>

                                            <TableRow>
                                                <TableCell component="th" scope="row">
                                                    <b>Rank:</b>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    {data.rating}
                                                </TableCell>
                                            </TableRow>


                                            <TableRow>
                                                <TableCell component="th" scope="row">
                                                    <b>Genres:</b>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    {data.genres.map(item => {
                                                        return `${item.name}, `
                                                    })}
                                                </TableCell>
                                            </TableRow>

                                        </TableBody>
                                    </Table>

                                </TableContainer>

                            </Box>
                        </Box>

                        <h1>About anime</h1>
                        <Typography>{data.synopsis}</Typography>
                    </Container>

                </>

            ):(null)

        )
    }
    const ProgressBar = ()=>{
        return(
            !isLoading ? (null):(<LinearProgress />)
        )
    }
    return (
        <>
            <ProgressBar/>
            <Card/>

        </>

    )
}